<template>
<infinite-loading ref="infiniteLoading" @infinite="infiniteHandler" spinner="circles" :force-use-infinite-wrapper="classParent">
    <div slot="spinner">
    </div>
    <div class="no-results" slot="no-results">{{$t('Common.Loading.NoResult')}}</div>
    <div class="no-results" slot="no-more"></div>
    <div class="error" slot="error" slot-scope="{ trigger }">
        {{$t('Common.Loading.Error')}}
        <a @click="trigger" href="javascript:;">{{$t('Common.Loading.Retry')}}</a>
    </div>
</infinite-loading>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading'

export default {
    name: 'InfiniteLoadingCustom',
    components: {
        InfiniteLoading
    },
    props: {
        classParent: null,
        isComplete: {
            type: Boolean,
            default: false
        },
        isError: {
            type: Boolean,
            default: false
        },
        isSearch: {
            type: Boolean,
            default: false
        },
        fetchData: {
            type: Function,
            default: () => {}
        }
    },
    watch: {
        async isSearch(val) {
            if (val && this.$refs['infiniteLoading']) {
                await this.$refs.infiniteLoading.stateChanger.reset();
            }
        }
    },
    methods: {
        async infiniteHandler($state) {
            await this.fetchData();
            if (this.isError) {
                $state.error()
            } else if (this.isComplete) {
                $state.complete()
            } else $state.loaded()
        }
    }
}
</script>

<style lang="scss">
.no-results,
.error {
    color: #666;
    font-size: 1.5rem;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
}
</style>
